@import '../../../styles/settings.scss';

.single-product {
	font-size: $base-size * 1.2;
	.card-title, .card-subtitle {
		font-size: $base-size * 1.3;
	}
	.card-text {
		margin: 0.6rem 0;
	}
	.btn {
		width: 8rem;
		margin: 1.25rem;
	}
}

@media (max-width: 479px) {
	.single-product {
		text-align: center;
		.card-text {
			font-size: $base-size;
		}
	}
}